import { ChangeDetectionStrategy, Component, HostListener, Input, OnDestroy } from '@angular/core';
import { transition, state, trigger, style, animate } from '@angular/animations';
import { GalleryService } from '../../service/gallery.service';

@Component({
  selector: 'gallery-modal',
  template: "\n    <div *ngIf=\"gallery.state | async as state\">\n\n      <div *ngIf=\"state.active\" class=\"g-modal\">\n\n        <div class=\"g-btn-close-container\">\n          <div *ngIf=\"closeButton\" class=\"g-btn-close\" (click)=\"gallery.close()\"></div>\n        </div>\n\n        <gallery-main [@popup] [state]=\"state\" [config]=\"gallery.config\"></gallery-main>\n\n        <div class=\"g-overlay\" (click)=\"gallery.close()\"></div>\n\n      </div>\n\n    </div>\n  ",
  styles: ["\n    *{box-sizing:border-box}.g-modal{position:fixed;left:0;right:0;top:0;bottom:0;background-color:rgba(0,0,0,.6);z-index:1;overflow:hidden;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center}@media (max-width:480px){.g-modal{padding:0}}.g-overlay{position:absolute;left:0;right:0;top:0;bottom:0}.g-btn-close-container{width:100%;display:-ms-flexbox;display:flex}@media (max-width:480px){.g-btn-close-container{-ms-flex-pack:end;justify-content:flex-end}.g-btn-close-container .g-btn-close{margin:.5em;position:relative;right:0;top:0}}.g-btn-close{position:absolute;right:1em;top:1em;z-index:2;cursor:pointer;width:30px;height:30px;transition:all .2s linear;background-image:url(data:image/svg+xml;base64,)}.g-btn-close:active{transform:rotate(180deg) scale(.9)}\n  "],
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [
    trigger('popup', [
      state('in', style({ opacity: 0.8 , transform: 'scale(0.2) translate3d(0, 100px, 0)'})),
      transition('void => *', [
        style({  opacity: 0.8 , transform: 'scale(0.2) translate3d(0, 100px, 0)' }),
        animate(300)
      ]),
      transition('* => void', [
        animate(300, style({  opacity: 1 , transform: 'scale(1)  translate3d(0, 0, 0)' }))
      ])
    ])]
})
export class GalleryModalComponent implements OnDestroy {

  @Input() closeButton = true;
  

  /** Activate keyboard for navigation */
  @HostListener('body:keydown', ['$event'])
  keyboardInput(event: KeyboardEvent) {
    if (!this.gallery.state.getValue().active){
      return;
    }
    event.stopPropagation();
    switch (event.keyCode) {
      case 37:  // prev
        this.gallery.prev();
        break;
      case 39:  // next
        this.gallery.next();
        break;
      case 13:  // enter
        this.gallery.next();
        break;
      case 27:  // esc
        this.gallery.close();
        break;
      default:
        return;
    }
  }

  constructor(public gallery: GalleryService) {
  }

  ngOnDestroy() {
    this.gallery.reset();
  }

}
